<div class="swiper-container">
  <div class="cover" v-if='showCover' @click='showCover = false' style="z-index: 999">
    <div class="arrow"></div>
    <div class="text">
      <p>ç¹å»å³ä¸è§èå</p>
      <p>å¨é»è®¤æµè§å¨ä¸­æå¼å¹¶å®è£åºç¨</p>
    </div>
  </div>
  <div class="rotate" v-if='showRotate' style="z-index: 1000">
