    <div class="swiper-slide slide2">
      <div :class='["title", "transition", step1 ? "fadeIn": "hide"]'>æ å¹¿å, 0ç§æ­æ¾</div>
      <div :class='["subTitle", "transition", step1 ? "fadeIn": "hide"]'>ç¾å¥½ççå½ä¸æµªè´¹å¨å¹¿åä¸</div>
      <div :class='["phone", step2 ? "fadeIn": "hide"]'>
        <img src="../../assets/m-official/phone.png" alt="">
      </div>
      <div :class='["element", "move", step3 ? "moveIn": "moveStart"]'>
        <img src="../../assets/m-official/element.png" alt="">
      </div>
      <div class="btn">
        <a href="javascript:void(0);" @click='handleDownLoadClick'>ä¸è½½APP</a>
      </div>
      <div class="next" @click='handleNextClick'></div>
      <div class="totop" @click='handlePrevClick'></div>
    </div>
    <div class="swiper-slide slide3">
      <div :class='["title", "transition", step4 ? "fadeIn": "hide"]'>åè´¹è§ç</div>
      <div :class='["subTitle", "transition", step4 ? "fadeIn": "hide"]'>å¨ç«è¶æ¸åå®¹åè´¹çï¼äº«åVIPçº§å¾é</div>
      <div :class='["movies"]'>
        <img :class='["movie1", "transition", step5 ? "fadeIn": "hide"]' src="../../assets/m-official/slide3-1.png" alt="">
        <img :class='["movie2", "transition", step6 ? "fadeIn": "hide"]' src="../../assets/m-official/slide3-2.png" alt="">
        <img :class='["movie3", "transition", step7 ? "fadeIn": "hide"]' src="../../assets/m-official/slide3-3.png" alt="">
        <div :class='["free", "transition", step8 ? "fadeIn": "hide"]'>FREE</div>
      </div>
      <div class="btn">
        <a href="javascript:void(0);" @click='handleDownLoadClick'>ä¸è½½APP</a>
      </div>
      <div class="next" @click='handleNextClick'></div>
      <div class="totop" @click='handlePrevClick'></div>
    </div>
    <div class="swiper-slide slide4">
      <div :class='["title", "transition", step9 ? "fadeIn": "hide"]'>å¨ç½è§é¢ï¼ä¸ç½æå°½</div>
      <div :class='["subTitle", "transition", step9 ? "fadeIn": "hide"]'>ç¬å®¶å®æï¼å¨ç½åå®¹ï¼å°½å¨æè±¡</div>
      <div :class='["icons", "transition", step10 ? "fadeIn": "hide"]'><img src="../../assets/m-official/icons.png" alt=""></div>
      <div :class='["pics", "transition", step11 ? "fadeIn": "hide"]'><img src="../../assets/m-official/pics.png" alt=""></div>
      <div class="btn">
        <a href="javascript:void(0);" @click='handleDownLoadClick'>ä¸è½½APP</a>
      </div>
      <div class="next" @click='handleNextClick'></div>
      <div class="totop" @click='handlePrevClick'></div>
    </div>
    <div class="swiper-slide slide5">
      <div :class='["title", "transition", "smallTitle", step12 ? "fadeIn": "hide"]'>å¤§åè§å¬çå®´ï¼å¨ç¨ç¬å®¶ç´æ­</div>
      <div :class='["subTitle", "transition", step12 ? "fadeIn": "hide"]'>çæ³é¾æ¥ï¼ç½®èº«ç°åº</div>
      <div :class='["icons2", "transition", step13 ? "fadeIn": "hide"]'><img src="../../assets/m-official/icons2.png" alt=""></div>
      <div :class='["pics2", "transition", step14 ? "fadeIn": "hide"]'><img src="../../assets/m-official/pics2.png" alt=""></div>
      <div class="btn">
        <a href="javascript:void(0);" @click='handleDownLoadClick'>ä¸è½½APP</a>
      </div>
      <div class="totop" @click='handlePrevClick'></div>
    </div>
  </div>
</div>
</template>

<script>
import Swiper from 'swiper'
import commonMixins from '@/mixins/common'
let mySwiper = null

export default {
  data() {
    return {
      step1: false, // slide2
      step2: false,
      step3: false,
      step4: false, // slide3
      step5: false,
      step6: false,
      step7: false,
      step8: false,
      step9: false, // slide4
      step10: false,
      step11: false,
      step12: false, // slide5
      step13: false,
      step14: false,
      showCover: false,
      showRotate: false
    }
  },

  mixins: [commonMixins],

  mounted() {
    const that = this

    this.fetchWechatShare()

    this.checkOrientation()

    window.addEventListener('orientationchange', () => {
      this.checkOrientation()
    })

    mySwiper = new Swiper('.swiper-container', {
      direction: 'vertical',
      loop: false,
      onSlideChangeStart(swiper) {
        for (let i = 1; i < 15; i++) {
          that[`step${i}`] = false
        }
        const activeIndex = swiper.activeIndex
        if (activeIndex === 1) {
          setTimeout(() => {
            that.step1 = true
          }, 300)

          setTimeout(() => {
            that.step2 = true
          }, 600)

          setTimeout(() => {
            that.step3 = true
          }, 700)
        } else if (activeIndex === 2) {
          setTimeout(() => {
            that.step4 = true
          }, 300)

          setTimeout(() => {
            that.step5 = true
          }, 600)

          setTimeout(() => {
            that.step6 = true
          }, 900)

          setTimeout(() => {
            that.step7 = true
          }, 1200)

          setTimeout(() => {
            that.step8 = true
          }, 1500)
        } else if (activeIndex === 3) {
          setTimeout(() => {
            that.step9 = true
          }, 300)

          setTimeout(() => {
            that.step10 = true
          }, 600)

          setTimeout(() => {
            that.step11 = true
          }, 900)
        } else if (activeIndex === 4) {
          setTimeout(() => {
            that.step12 = true
          }, 300)

          setTimeout(() => {
            that.step13 = true
          }, 600)

          setTimeout(() => {
            that.step14 = true
          }, 900)
        } else {
          for (let i = 1; i < 15; i++) {
            that[`step${i}`] = false
          }
        }
      }
    })

    console.log(mySwiper)
  },
  methods: {
    checkOrientation() {
      if (window.orientation === 180 || window.orientation === 0) {
        this.showRotate = false
      } else {
        this.showRotate = true
      }
    },
    fetchWechatShare() {
      var oReq = new XMLHttpRequest()

      oReq.onload = (e) => {
        var data = oReq.response
        console.log(data, typeof data)
        if (data) {
          /* global wx:true */
          try {
            data = JSON.parse(data)
          } catch (e) {
            data = {}
          }
          wx.config({
            // debug: true,
            appId: 'wxc89ca66545c2df54',
            timestamp: data.timestamp,
            nonceStr: data.nonceStr,
            signature: data.signature,
            jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone']
          })

          wx.onMenuShareAppMessage({
            title: 'æè±¡è§é¢âæè±¡æä¸å',
            desc: 'å¨æ°VIPè§é¢å¹³å°ï¼æµ·éè§é¢åè´¹çäº«',
            imgUrl: 'http://dx-image-test.itangchao.me/logo.jpg'
          })
          wx.onMenuShareTimeline({
            title: 'æè±¡è§é¢âæè±¡æä¸å',
            imgUrl: 'http://dx-image-test.itangchao.me/logo.jpg'
          })
        }
      }
      oReq.open('POST', '/wechat/get_params')
      oReq.send()
    },
    handleNextClick() {
      mySwiper.slideNext()
    },
    countDownload() {
      var oReq = new XMLHttpRequest()
      const url = location.href
      if (url.indexOf('?') > -1) {
        oReq.open('GET', url + '&downclick=1&t=' + ((new Date()).getTime()))
      } else {
        oReq.open('GET', url + '?downclick=1&t=' + ((new Date()).getTime()))
      }
      oReq.send()
    },
    handleDownLoadClick() {
      const ua = navigator.userAgent
      let channel = this.getUrlParam('channel')
      this.countDownload()
      if (/MicroMessenger/i.test(ua)) {
        location.href = 'http://a.app.qq.com/o/simple.jsp?pkgname=com.daxiang.live'
      } else if (/android/i.test(ua)) {
        if (channel) {
          location.href = `http://dx-downloads-test.itangchao.me/daxiang-live-A_${channel}-release.apk`
        } else {
          location.href = 'http://dx-downloads-test.itangchao.me/daxiang-live-A_OFFICAL-release.apk'
        }
      } else if (/iPhone/i.test(ua)) {
        if (channel) {
          location.href = `https://itunes.apple.com/app/apple-store/id1235011792?pt=118654704&ct=I_${channel}&mt=8`
        } else {
          location.href = 'https://itunes.apple.com/us/app/id1235011792?l=zh&ls=1&mt=8'
        }
      }
    },
    handlePrevClick() {
      mySwiper.slideTo(0)
    }
  }
}
</script>

<style lang="postcss" module>
